.Card {
    width: 30%;
    z-index: 100;
    height: 100px;
    border-radius: 5px;
    background-color: rgba(198, 213, 214, 0.904);
    border: 1.5px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 10px;
    box-sizing: border-box;
    margin: 0 1.6%;
    text-align: center;
    cursor: pointer;
}